
#indicators {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

  #indicators td {
    border: 1px solid #ffffff;
  }

  #indicators thead th:nth-child(1) {
    width: 63%;
  }

  #indicators thead th:nth-child(2) {
    width: 7%;
  }

  #indicators thead th:nth-child(n + 3) {
    width: 30%;
  }

  #indicators th, td {
    padding: 6px;
  }

  #indicators th p {
    text-align: center;
  }

  #indicators th.border {
    border-left: 1px solid #000000;
    border-right: 1px solid #000000;
  }

  #indicators tbody tr {
  }

  #indicators tbody tr:hover {
    background-color: var(--nsw-palette-purple-04) !important;
      cursor: pointer;
  }
