.outcome rect {
    stroke: var(--nsw-palette-red-02);
    stroke-width: 2;
    cursor: pointer;
}

.outcome rect:hover {
    stroke: var(--nsw-palette-red-02);
    stroke-width: 2;
    cursor: pointer;
}

.outcome text {
  pointer-events: none
}

.outcome foreignObject {
  pointer-events: none
}

.outcomes-content g {
    font-size: var(--nsw-font-size-xxs-desktop);
}

.outcomes-box {
    padding: 5px 3px;
}

