.highlight-count {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    background-color: #ffffff;
    color: var(--nsw-palette-purple-01);
    border-radius: 100%;
    -moz-border-radius: 100%;
    -webkit-border-radius: 100%;
    text-align: center;
    border: 4px solid var(--nsw-palette-purple-01);
    font-size: 24px;
    font-weight: bold;
    margin-left: 10px;
}

.indicator-table-checkbox {
  text-align: center;
}



#indicator-table {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

#indicator-table td {
  border: 1px solid #999;
}

#indicator-table thead th:nth-child(1) {
  width: 3%;
}

#indicator-table thead th:nth-child(2) {
  width: 10%;
}

#indicator-table thead th:nth-child(3) {
  width: 30%;
}

#indicator-table thead th:nth-child(4) {
  width: 10%;
}

#indicator-table thead th:nth-child(5) {
  width: 57%;
}

#indicator-table th, td {
  padding: 6px;
}

#indicator-table tbody tr {
    background-color: #ffffff;
}

#indicator-table tbody tr:hover {
    background-color: var(--nsw-palette-blue-04);
    box-sizing: border-box;
}

.indicator-table-button {
  margin: 5px 0;
}