.selected {
    background-color: var(--nsw-palette-purple-02);
    font-weight: bold;
    color: #ffffff;
}

.not-selected {

}
.data-avail {
  background-color: var(--nsw-palette-green-02);
}

.data-unavail {
  background-color: var(--nsw-palette-red-02);
}

table .nsw-button--light.nsw-button.nsw-button--small,
table .nsw-button--dark.nsw-button.nsw-button--small,
table .nsw-button--dark-outline-solid.nsw-button.nsw-button--small{
    min-width: auto;
}

.ReactModal__Overlay.ReactModal__Overlay--after-open {
    background-color: rgba(0, 0, 0, 0.5) !important;
}

table.modal-table tr td:first-child {
    background-color: var(--nsw-palette-purple-04);
    width: 300px;
}

.ReactModal__Content.ReactModal__Content--after-open {
    padding-bottom: 0px !important;
}

.nsw-position-sticky {
    background-color: rgba(255,255,255,0.8);
    bottom: 0px;
}

