.domain-button {
    width: 100px !important;
    height: 50px;
}

.domain-button rect:hover {
/*
  stroke: #ffffff;
  stroke-width: 3px;
*/
  cursor: pointer;    
  fill-opacity: 1;
}

.domain-button circle {
  fill-opacity: 0.3;
  pointer-events: none;
}

.domain-button path {
  pointer-events: none;
  fill-opacity: 1;
}