/* Landing Page = START */

.nsw-hero-banner {
    background-color: rgba(0,30,100,0.6);
    margin-bottom: 10px;
    max-width: 100vw;
    background-image: url(./assets/students-02.jpg);
    background-position: center 30%;
    background-size: 100%;
    background-blend-mode: darken;
}

/* Landing Page = END */

.full-width {
    width: 100%;
    background-color: #ffffff;
}

.bg-blue-04 {
    background-color: var(--nsw-text-link-hover);
}

.App {
}

.btn-back {
    font-size: var(--nsw-font-size-xs-desktop);
    color: #ffffff !important;
}

.btn-back a {
    color: #ffffff
}

.main-masthead {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px 120px;
    color: #ffffff;
    background-color: var(--nsw-palette-blue-01);
    
}

.main-header {
    padding: 24px 120px;
    color: #ffffff;
    background-color: var(--nsw-palette-purple-01);
}

.main-app {    
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ffffff;
    padding: 16px 180px !important;
}

.outcomes-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--nsw-palette-purple-02);
    color: #ffffff;
    padding: 16px 180px !important;
}


.outcomes {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--nsw-palette-white);
    padding: 0px 180px !important;
}


.indicators {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--nsw-palette-purple-04);
    padding: 20px 180px 40px !important;
}

.results {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--nsw-palette-purple-03);
    padding: 20px 180px 40px !important;
}

.domain-container {
    width: 1400px;
    height: 126px
}

.outcome-container {
    width: 1400px;
    height: 400px
}

.joinlines {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: 50px;
    width: 1260px;
    margin: 0px auto;
}

.box-1 {
    border-right: 3px var(--nsw-palette-purple-02) solid;
}

.indicator-panel {
    border: 4px var(--nsw-palette-purple-02) solid;
    width: 1400px;
    background-color: #ffffff;
    padding: 5px 20px;
    border-radius: 20px;
}

#indicators tbody tr:nth-child(even) {
    background-color: #f1f1f1;
}

.content {
    margin-bottom: 20px;
}

.nsw-tooltip {
    border-bottom: none;
}

span.material-icons.nsw-material-icons.nsw-in-page-alert__icon {
    position: relative;
    top: 6px;
    cursor: pointer;
    margin-right: 0px;
}

div > img {
  max-width: 100%;
  max-height: 100%;
}

.tool-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    background-color: var(--nsw-palette-purple-03);
}

h4 {
    width: 100%;
    text-align: center;
    padding: 10px 0px;
    background-color: var(--nsw-palette-purple-03);
    color: var(--nsw-palette-purple-01);
    margin: 0px;
}

@media only screen and (max-width: 1280px) {
    
    .main-app {    
        padding: 16px 40px !important;
    }
    
    .outcomes {
        padding: 40px 40px 0px !important;
    }
    
    .domain-container {
        width: 1200px;
    }
    
    .indicators {
        padding: 16px 40px !important;
    }
    
    .indicator-panel {
        width: 1200px;
    }
    
    .results {
        padding: 16px 40px !important;
    }
    
}